import freeIconButton from "./free-icon-button.vue"
import freePopup from "./free-popup.vue"
export default {
	props: {
		showBack:{
			type:Boolean,
			default:false
		},
		title: {
			type: [String,Boolean],
			default:false 
		},
		fixed:{
			type:Boolean,
			default:true
		},
		noreadnum:{
			type:Number,
			default:0
		},
		bgColor:{
			type:String,
			default:"bg-light"
		},
		showRight:{
			type:Boolean,
			default:true
		}
	},
	components:{
		freeIconButton,
		freePopup
	},
	data() {
		return {
			statusBarHeight:0,
			navBarHeight:0,
			menus:[
				{
					name:"发起群聊",
					event:"",
					icon:"\ue600"
				},
				{
					name:"添加好友",
					event:"",
					icon:"\ue6a5"
				}
			],
		}
	},
	mounted() {



		this.navBarHeight = this.statusBarHeight + uni.upx2px(90)
	},
	computed: {
		fixedStyle() {
			return `height:${this.navBarHeight}px`
		},
		getTitle(){
			let noreadnum = this.noreadnum > 0 ? '('+this.noreadnum+')' : ''
			return this.title
		},
		getClass(){
			let fixed = this.fixed?'fixed-top':''
			return `${fixed} ${this.bgColor}` 
		}
	},
	methods: {
		openExtend() {
			this.$refs.extend.show(uni.upx2px(415),uni.upx2px(150))
		},
		// 返回
		back(){
			uni.navigateBack({
				delta: 1
			});
		}
	},
}
